@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&family=Lato&family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
    position: fixed;
    left: 0px;
    top: 0px;
    background: white;
    border-right: lightgray 1px solid;
    display: grid;
    width: 20%;
    height: 100vh;
    grid-template-columns: auto;
    grid-template-rows: auto auto auto auto;
}

.navBarElem {
    background: transparent;
    border: 0px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    transition: background-color .9s;
    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}

.navBarElem > p {
    font-family:'Quicksand', sans-serif;
    font-size: 3vh;
    flex-grow: 0.5;
    /*transform: translateX(-100%);*/
    transition: transform .5s;
    transition-timing-function: ease;
}

.elemBack {
    background-color: darkred;
    width: 0%;
    height: 1px;
    transition: width .5s;
    transition-timing-function: ease;
}

.navBarElem:hover > .elemBack{
    width: 100%;
}

.navBarElem:hover > p{
    transform: translateX(0%);
}

.navBarElem:hover {
    background-color: lightgrey;
}


.main-screen-content {
    width: calc(80% - 15px);
    margin-left: calc(20% + 15px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-screen-centerContent {
    height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.main-screen-content > p {
    font-family: 'Lato' ,sans-serif;
    font-size: 3.2vh;
    text-align: center;
}

.main-screen-content > h1 {
    font-family: 'Jost' ,sans-serif;
    width: 100%;
    font-size: 7vh;
    text-align: center;
}


.content-page-content {
    font-family: 'Lato' ,sans-serif;
    width: calc(60% - 15px);
    margin-top: 15vh;
    margin-left: calc(25% + 15px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    line-height: 2;
    text-align: justify;
}

.content-page-header {
    font-family: 'Jost' ,sans-serif;
}

.document {
    width: calc(80% - 15px);
    height: 100vh;
    margin-left: calc(20% + 15px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
}

.document > * {
    width: 90%;
    height : 90vh;
    border: none;
    zoom: 60;
}
#pageCover {
    position:fixed;
    width: 0%;
    height: 0vh;
    z-index: 1000;
    background-color: transparent;
}
